import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { API } from '../App';
import YouTube from 'react-youtube';

const Youtube = ({ video }) => {
  const videoId = video.videoId;
  // console.log(videoId);
  const [formats, setFormats] = useState([]);
  const [format, setFormat] = useState(null);

  const getFormat = (id) => {
    axios
      .get(`${API}/getformat/${id}`)
      .then((res) => {
        let ft = res.data.result;
        setFormats(ft);
        setFormat(ft[0].itag);
        // console.log(vd[0]);
      })
      .catch((error) => console.log(error));
  };

  const downloadVideo = async (e) => {
    let location = `${API}/download?id=${videoId}&q=${format}`;
    window.location.replace(location);
  };

  useEffect(() => {
    getFormat(videoId);
  }, []);

  return (
    <div className='bg-white rounded p-2 shadow-xl'>
      <div className='mt-4 relative h-0' style={{ paddingBottom: '56.25%' }}>
        <YouTube
          videoId={videoId}
          className='absolute h-full w-full top-0 left-0'
        />
        {/* <iframe
          title='test'
          width='560'
          className='absolute h-full w-full top-0 left-0'
          height='349'
          src={`http://www.youtube.com/embed/${videoId}?rel=0&hd=1`}
          frameBorder='0'
          allowFullScreen
        ></iframe> */}
      </div>
      <h2 className='text-xl text-gray-800'>{video.title}</h2>
      <h4 className='text-xs text-gray-400'>
        {video.author.name} {`(${video.ago})`}
        <span className='text-gray-600 ml-2'>Time: {video.timestamp}</span>
      </h4>
      <h4 className='text-sm text-gray-500'>{video.description}</h4>
      {format && (
        <div className='flex justify-center'>
          <div className='relative'>
            <select
              onChange={(e) => setFormat(e.target.value)}
              className='bg-gray-800 px-4 py-2 cursor-pointer rounded-l text-gray-200 appearance-none w-full focus:outline-none focus:border-gray-500'
            >
              {formats.map((dt) => (
                <option key={dt.itag} value={dt.itag}>
                  {dt.qualityLabel} ({dt.container})
                </option>
              ))}
            </select>
            <div className='pointer-events-none absolute inset-y-0 right-0 flex items-center ml-2 px-2 text-gray-300'>
              <svg
                className='fill-current h-4 w-4'
                xmlns='http://www.w3.org/2000/svg'
                viewBox='0 0 20 20'
              >
                <path d='M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z' />
              </svg>
            </div>
          </div>
          <button
            onClick={downloadVideo}
            className='bg-green-800 px-4 py-2 rounded-r text-gray-200'
          >
            Download
          </button>
        </div>
      )}
    </div>
  );
};

export default Youtube;
