import React from 'react';

const VideoList = ({ video, selectVideo }) => {
  return (
    <div
      onClick={(e) => selectVideo(video)}
      className='cursor-pointer py-2 bg-white my-1 flex items-center rounded shadow'
    >
      <img src={video.thumbnail} alt={video.title} className='w-2/6 h-24' />
      <div className='w-4/6 p-2 flex flex-col'>
        <h3 className='text-base m-0 text-blue-900'>
          {video.title.substring(0, 40)}...
        </h3>
        <p className='text-xs text-gray-600'>{video.author.name}</p>
        <small className='text-xs text-gray-500'>
          {video.ago}
          <span className='text-gray-600 ml-2'>Time: {video.timestamp}</span>
        </small>
      </div>
    </div>
  );
};

export default VideoList;
