import React, { useState, useEffect } from 'react';
import YouTube from './components/Youtube';
import VideoList from './components/VideoList';
import axios from 'axios';

export const API = process.env.API || 'http://addyytdl-b.herokuapp.com';

const App = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [videos, setVideos] = useState([]);
  const [selected, setVideo] = useState(null);
  // `https://www.googleapis.com/youtube/v3/search?part=snippet&q=${term}&key=${API_KEY}`,
  const searchTube = (term) => {
    axios
      .get(`${API}/video?s=${term}`)
      .then((res) => {
        let vd = res.data.result;
        setVideo(vd[0]);
        setVideos(vd);
        setSearchTerm('');
        // console.log(vd[0]);
      })
      .catch((error) => {
        alert('Unable to load videos!!');
        console.log(error);
      });
  };

  const getKeyPress = (e) => {
    if (e.key === 'Enter') {
      searchTube(searchTerm);
    }
  };

  const selectVideo = (vd) => {
    setVideo(vd);
  };

  useEffect(() => {
    searchTube('React js');
  }, []);

  return (
    <div className='bg-gray-100 min-h-screen'>
      <h1 className='text-3xl py-3 text-center bg-gray-700 text-gray-200'>
        Simple YTube Downloader
      </h1>
      <div className='container px-3 m-auto'>
        <div className='grid gap-2 grid-cols-4 justify-center'>
          <div className='col-span-4'>
            <input
              type='text'
              placeholder='Search For Video'
              value={searchTerm}
              onKeyDown={getKeyPress}
              onChange={(e) => setSearchTerm(e.target.value)}
              className='bg-gray-300 text-base rounded-l p-2 border-b-2 border-gray-600 w-4/5'
            />
            <button
              className='px-2 py-2 bg-gray-800 rounded-r text-gray-200 w-1/5'
              onClick={(e) => searchTube(searchTerm)}
            >
              Search
            </button>
          </div>
          {!selected && (
            <div className='w-full h-full text-gray-800 col-span-4 top-0 left-0 py-32 flex justify-center align-middle'>
              Please Search A Video!
            </div>
          )}
          {selected && (
            <React.Fragment>
              <div className='col-span-4 md:col-span-3'>
                <YouTube video={selected} />
              </div>
              <div className='col-span-4 flex flex-col md:col-span-1'>
                {videos.map((vd) => (
                  <VideoList
                    key={vd.title}
                    selectVideo={selectVideo}
                    video={vd}
                  />
                ))}
              </div>
            </React.Fragment>
          )}
        </div>
      </div>
      <div className='col-span-4 text-sm text-center mt-4 text-gray-200 py-4 bg-gray-900'>
        &copy; Copyright {new Date().getFullYear()}{' '}
        <a
          href='http://eliteaddy.com'
          target='_blank'
          rel='noopener noreferrer'
          className='border-b text-gray-200'
        >
          EliteAddy Inc
        </a>
      </div>
    </div>
  );
};

export default App;
